import React, { useState } from "react";
import { Document, Page, pdfjs } from "react-pdf";
import "./Certifications.css";

//  Correctly setting the PDF worker location
pdfjs.GlobalWorkerOptions.workerSrc = `${process.env.PUBLIC_URL}/pdf.worker.min.js`;

const certificates = [
  { id: 1, title: "Introduction to JAVA", file: `${process.env.PUBLIC_URL}/certs/IntroToJava.pdf` },
  { id: 2, title: "Data Structures in JAVA", file: `${process.env.PUBLIC_URL}/certs/DS.pdf` },
  { id: 3, title: "Next Generation Platform for IBM Sterling Order Management on Cloud Specialist", file: `${process.env.PUBLIC_URL}/certs/OMoC.pdf` },
  { id: 4, title: "Using the Self Service Tool on Next Generation Platform", file: `${process.env.PUBLIC_URL}/certs/SST.pdf` },
  { id: 5, title: "Setting up the Developer Toolkit on IBM Sterling OMoC", file: `${process.env.PUBLIC_URL}/certs/toolkit.pdf` },
];

const Certifications = () => {
  const [selectedCert, setSelectedCert] = useState(null);
  const [numPages, setNumPages] = useState(null);
  const [pageNumber, setPageNumber] = useState(1);

  return (
    <div className="container">
      <h1 className="cert-title">My Certifications</h1>
      <p className="cert-subtitle">Click on a certification to view the full document.</p>

      <div className="cert-grid">
        {certificates.map((cert) => (
          <div key={cert.id} className="cert-card" onClick={() => setSelectedCert(cert)}>
            <h3 className="cert-title">{cert.title}</h3>
          </div>
        ))}
      </div>

      {/*  PDF Modal Popup */}
      {selectedCert && (
        <div className="pdf-modal" onClick={() => setSelectedCert(null)}>
          <div className="pdf-content" onClick={(e) => e.stopPropagation()}>
            <button className="close-btn" onClick={() => setSelectedCert(null)}></button>

            {/*  Display PDF Inside Modal */}
            <Document
              file={selectedCert.file}
              onLoadSuccess={({ numPages }) => setNumPages(numPages)}
              className="pdf-document"
            >
              <Page pageNumber={pageNumber} width={700} />
            </Document>

            {/*  PDF Controls */}
            <div className="pdf-controls">
              <button onClick={() => setPageNumber(pageNumber > 1 ? pageNumber - 1 : 1)}> Prev</button>
              <span> Page {pageNumber} of {numPages} </span>
              <button onClick={() => setPageNumber(pageNumber < numPages ? pageNumber + 1 : numPages)}>Next </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default Certifications;
