import React, { useState } from "react";
import { Button, TextInput, TextArea } from "@carbon/react";

export const Contact = () => {
  const [formData, setFormData] = useState({
    name: "",
    email: "",
    message: "",
  });

  const [submitted, setSubmitted] = useState(false);

  const handleChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    // Here, you can integrate a backend service like Formspree, EmailJS, or a custom API
    console.log("Message Sent:", formData);
    setSubmitted(true);
  };

  return (
    <div className="container contact">
      <h1>Contact Me</h1>
      <p>Have a question or want to collaborate? Reach out below!</p>

      {submitted ? (
        <p className="success-message">Thank you! Your message has been sent.</p>
      ) : (
        <form onSubmit={handleSubmit} className="contact-form">
          <TextInput
            id="name"
            name="name"
            labelText="Your Name"
            value={formData.name}
            onChange={handleChange}
            required
          />
          <TextInput
            id="email"
            type="email"
            name="email"
            labelText="Your Email"
            value={formData.email}
            onChange={handleChange}
            required
          />
          <TextArea
            id="message"
            name="message"
            labelText="Your Message"
            value={formData.message}
            onChange={handleChange}
            required
          />
          <Button kind="primary" type="submit">Send Message</Button>
        </form>
      )}

      <div className="contact-info">
        <h2>Connect with Me</h2>
        <ul>
          <li><a href="mailto:gulshansharma011@gmail.com"> Email</a></li>
          <li><a href="https://github.com/GulshanShrma" target="_blank" rel="noopener noreferrer"> GitHub</a></li>
          <li><a href="https://www.linkedin.com/in/gulshan-shrma/" target="_blank" rel="noopener noreferrer"> LinkedIn</a></li>
        </ul>
      </div>
    </div>
  );
};
